const Recommendations = {
    getTextByAlpha(alpha) {
        if (alpha <= 1) {
            return '    .   .';
        } else if (1 < alpha && alpha <= 2) {
            return '  ,          ,     .        ,       ,      .   -     ,   20%  .';
        }  else if (2 < alpha && alpha <= 3) {
            return '     ,       -    .      .';
        } else {
            return '    ,       .      ,     .';
        }
    },

    getColorByAlpha(alpha) {
        if (alpha <= 1) {
            return 'gray-300';
        } else if (1 < alpha && alpha <= 2) {
            return 'red-300';
        }  else if (2 < alpha && alpha <= 3) {
            return 'orange-300';
        } else {
            return 'green-300';
        }
    }
    
}

export default Recommendations;